import React, { useState, useEffect } from 'react';
import { AppBar, Select, Toolbar, Typography, MenuItem, makeStyles } from '@material-ui/core';
import { Container } from 'react-bootstrap';
import './Header.css';
import { useHistory } from 'react-router-dom';
import { createTheme, ThemeProvider } from '@material-ui/core/styles';
import { CryptoState } from '../CryptoContext';
import AuthModal from './Authentication/AuthModal';
import UserSidebar from './Authentication/UserSidebar';

const useStyles = makeStyles((theme) => ({
  title: {
    flex: 1,
    color: 'gold',
    fontFamily: 'Montserrat',
    fontWeight: 'bold',
    cursor: 'pointer',
  },
  select: {
    '& .MuiSelect-select.MuiSelect-select': {
      color: '#fff',
    },
  },
}));

const darkTheme = createTheme({
  palette: {
    primary: {
      main: '#fff',
    },
    type: 'dark',
  },
});

function Header() {
  const { currency, setCurrency ,user} = CryptoState();
  const history = useHistory();
  const classes = useStyles();
  //console.log(currency);

  const [isSmallScreen, setIsSmallScreen] = useState(false);

  useEffect(() => {
    const checkScreenSize = () => {
      setIsSmallScreen(window.innerWidth <= 768); // Adjust the breakpoint as needed
    };

    checkScreenSize();
    window.addEventListener('resize', checkScreenSize);
    return () => {
      window.removeEventListener('resize', checkScreenSize);
    };
  }, []);

  return (
    <ThemeProvider theme={darkTheme}>
      <AppBar color="transparent" position="static">
        <Container>
          <Toolbar className="header">
            {isSmallScreen ? (
              <Typography
                onClick={() => history.push('/')}
                className={classes.title}
                variant='5'
                id="logo"
              >
                CryptoC
              </Typography>
            ) : (
              <Typography
                onClick={() => history.push('/')}
                className={`logo ${classes.title}`}
                variant='5'
                id="logo"
              >
                <img
                  src={process.env.PUBLIC_URL + '/images/cryptoC_Logos.jpg'}
                  alt="Logo"
                />
              </Typography>
            )}
            <Select
              variant="outlined"
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              className={classes.select} // Add the custom CSS class to the Select component
              style={{ width: 100, height: 40, marginRight: 20, marginLeft: 15 }}
              value={currency} // Set the default value to 'USD'
              onChange={(e) => setCurrency(e.target.value)}
            >
              <MenuItem value={'USD'} style={{ color: '#fff' }}>USD</MenuItem> 
              <MenuItem value={'INR'} style={{ color: '#fff' }}>INR</MenuItem> 
            </Select>
            {user ? <UserSidebar /> : <AuthModal />}

           
          </Toolbar>
        </Container>
      </AppBar>
    </ThemeProvider>
  );
}

export default Header;
