import { useEffect, useState } from 'react';
import { makeStyles, createTheme } from '@material-ui/core/styles';
import { TableCell, TableContainer, Table, TableHead, TableRow, ThemeProvider, TableBody, Pagination } from '@mui/material';
import { Typography, Container, LinearProgress, TextField } from '@material-ui/core';
//import axios from 'axios';
//import { CoinList } from '../config/api';
import { useHistory } from 'react-router-dom';
import { CryptoState } from '../CryptoContext';

export function numberWithCommas(x) {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export default function CoinsTable() {
  
  const [search, setSearch] = useState("");
  const { currency, symbol,coins,loading,fetchCoins } = CryptoState();
  const [page,setPage]=useState(1);



 

  useEffect(() => {
    fetchCoins();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currency]);

  const darkTheme = createTheme({
    palette: {
      primary: {
        main: "#fff",
      },
      type: "dark",
    },
  });

  const handleSearch = () => {
    return coins.filter((coin) => {
      const lowerCaseSearch = search.toLowerCase();
      return (
        coin.name.toLowerCase().includes(lowerCaseSearch) ||
        coin.symbol.toLowerCase().includes(lowerCaseSearch)
      );
    });
  };
  

  const useStyles = makeStyles({
    row: {
      backgroundColor: "#16171a",
      cursor: "pointer",
      "&:hover": {
        backgroundColor: "#131111",
      },
      fontFamily: "Montserrat",
    },
    pagination: {
      "& .MuiPaginationItem-root": {
        color: "gold",
      },
      
    },
  });

  const history = useHistory();
  const classes = useStyles();

  return (
    <ThemeProvider theme={darkTheme}>
      <Container style={{ textAlign: "center" }}>
        <Typography variant="h4" style={{ margin: 18, fontFamily: 'Montserrat', color: '#EEEEEA' }}>
          Cryptocurrency Prices by Market Cap
        </Typography>

        <TextField
          label="Search For a Crypto Currency.."
          variant="outlined"
          style={{
            marginBottom: 20,
            width: "100%",
          }}
          InputLabelProps={{
            style: { color: "#EEEEEA" },
          }}
          InputProps={{
            style: {
              color: "#EEEEEA",
              borderColor: "#EEEEEA",
            },
          }}
          inputProps={{
            style: {
              color: "#EEEEEA",
              "&::placeholder": { color: "#EEEEEA" },
            },
          }}
          onChange={(e) => setSearch(e.target.value)}
        />

        <TableContainer>
          {loading ? (
            <LinearProgress style={{ backgroundColor: "gold" }} />
          ) : (
            <Table>
              <TableHead style={{ backgroundColor: "#EEBC1D" }}>
                <TableRow>
                  {["Coin", "Price", "24h Change", "Market Cap"].map((head) => (
                    <TableCell
                      style={{
                        color: "black",
                        fontWeight: "700",
                        fontFamily: "Montserrat",
                      }}
                      key={head}
                      align={head === "Coin" ? "" : "right"}
                    >
                      {head}
                    </TableCell>
                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {handleSearch()
                .slice((page-1)*10,(page-1)*10+10)
                .map((row) => {
                  const profit = row.price_change_percentage_24h > 0;

                  return (
                    <TableRow
                      onClick={() => history.push(`/coins/${row.id}`)}
                      className={classes.row}
                      key={row.name}
                    >
                      <TableCell component="th" scope="row" style={{ display: "flex", alignItems: "center", gap: 15 }}>
                        <img src={row?.image} alt={row.name} height="50" style={{ marginBottom: 10 }} />
                        <div style={{ display: "flex", flexDirection: "column" }}>
                          <span style={{ textTransform: "uppercase", fontSize: 22 }}>{row.symbol}</span>
                          <span style={{ color: "darkgrey" }}>{row.name}</span>
                        </div>
                      </TableCell>
                      <TableCell align="right">
                        {symbol} {numberWithCommas(row.current_price.toFixed(2))}
                      </TableCell>
                      <TableCell
                        align="right"
                        style={{
                          color: profit > 0 ? "rgb(14, 203, 129)" : "red",
                          fontWeight: 500,
                        }}
                      >
                        {profit && "+"}
                        {row.price_change_percentage_24h.toFixed(2)}%
                      </TableCell>
                      <TableCell align="right">
                        {symbol} {numberWithCommas(row.market_cap.toString().slice(0, -6))} M
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          )}
        </TableContainer>
        <Pagination
          count={(handleSearch()?.length / 10).toFixed(0)}
          style={{
            padding: 20,
            width: "100%",
            display: "flex",
            justifyContent: "center",
          }}
          classes={{ ul: classes.pagination }}
          onChange={(_, value) => {
            setPage(value);
            window.scroll(0, 450);
          }}
        />

        
      </Container>
    </ThemeProvider>
  );
}